              link: link, // JS
              imgUrl: imgUrl, // 
              success: function () {
                // 
              },
              cancel: function () {
                // 
              }
            });
            wx.onMenuShareAppMessage({
              title: title, // 
              desc: desc, // 
              link: link, // JS
              imgUrl: imgUrl, // 
              type: '', // ,musicvideolinklink
              dataUrl: '', // typemusicvideo
              success: function () {
