import Vue from 'vue'
import Vuex from 'vuex'
import local from  '../common/js/localStorage'
// import * as actions from './actions'
// import * as getters from './getters'

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  state: {
     city:'',
    //id
     cbid:'',
     id:'',
     key:0,
     cliuliang:'',
     cyuyin:'',
     cdx:'',
     code:'',
    //
     dzprice:'',
     ycprice:0,
    //id
     ycid:'',
    //
    school:'',
    schoolId:'',
    //   
    sf:'',
    // 
    userid:'',
    token:'',
    nickname:'',
    //
    Msg:'',
    //
    combotype:'dz',
    //
    invitecode:'',
    jifen:'',
  //  
    orderno:'',
    //
    names:'',
    cardid:'',
    validtime:'',
    address:'',
    phone:'',
    rxyear:'',
    major:'',
    sfs:'',
    //
    detailnum:0,
    //id
    riid:'',
  },
  mutations: {
    //
    city(state,now){
      state.city=now;
    },
    changeId(state,now){
      state.id=now;
    },
    //ceshi
    names(state,now){
      state.names=now;
    },
    cardid(state,now) {
      state.cardid=now;
    },
    validtime(state,now) {
      state.validtime=now;
    },
    address(state,now) {
      state.address=now;
    },
    phone(state,now) {
      state.phone=now;
    },
    rxyear(state,now) {
      state.rxyear=now;
    },
    major(state,now) {
      state.major=now;
    },
    sfs(state,now) {
      state.sfs=now;
    },
    //id 
    //id
    cbid(state,now){
      state.cbid=now;
      local.setItem('cbid',now)

    },
    //key
    changeKey(state,now){
      state.key=now;

    },
    //
    combotype(state,now){
      state.combotype=now;

    },
    //
    cliuliang(state,now){
      state.cliuliang=now;

    },
    cyuyin(state,now){
      state.cyuyin=now;

    },
    cdx(state,now){
      state.cdx=now;

    },
    code(state,now){
      state.code=now;

    },
    //
    dzprice(state,now){
      state.dzprice=now;

    },
    ycid(state,now){
      state.ycid=now;

    },
    //
    ycprice(state,now){
      state.ycprice=now;

    },
    // 
    school(state,now){
      state.school=now;
      local.setItem('school',now)
    },
    schoolId(state,now){
      state.schoolId=now;

    },
    //
    sf(state,now){
      state.sf=now;

    },
    // useid
    userid(state,now){
      state.userid=now;
      local.setItem('userids',now)
    },
    token(state,now){
      state.token=now;
      local.setItem('tokens',now)
    },
    nickname(state,now){
      state.nickname=now;

    },
    //
    Msg(state,now){
      state.Msg=now;

    },
    //
    invitecode(state,now){
      state.invitecode=now;

    },
    jifen(state,now){
      state.jifen=now;

    },
    //
    orderno(state,now){
      state.orderno=now;
      local.setItem('orderno',now)
    },
    //
    detailnum(state,now){
      state.detailnum=now;
      local.setItem('detailnum',now)
    },
    //id
    riid(state,now){
      state.riid=now;
    },
    },
  actions: {
    userid ({commit},now) {
         commit('userid',now)
    },
    token ({commit},now) {
         commit('token',now)
    },
    //
    Msg ({commit},now) {
      commit('Msg',now)
    },
    //
    invitecode ({commit},now) {
      commit('invitecode',now)
    },
    jifen ({commit},now) {
      commit('jifen',now)
    },

  }

})



