<template>
  <div class="tc-price">
    <div v-title>SIM</div>
    <h3></h3>
    <span class="title"></span>
    <div class="tc-choice">
      <div class="list">
        <span class="mar"></span>
        <span class="pri right">500MB</span>
      </div>
      <div class="list">
        <span class="mar"></span>
        <span class="pri right">100</span>
      </div>
      <div class="list">
        <span class="mar"></span>
        <span class="pri right">10</span>
      </div>
      <p class="detail">1.5GAPP</p>
      <p class="detail">100MAPP</p>
      <p class="detail"><span>59.00</span></p>
      <hr>
      <div class="tw">
        <span></span>
        <p>
          0.15//0.1/0.0001
          /KB10000
          </p>
      </div>
    </div>
  </div>
</template>

<script>
  import A from '../../directive'
  export default {

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
  .tc-price{
    h3{
      &:after{
        content:'(39)';
        display:inline-block;
        font-size: 12px;
        color: #F5A623;
        padding-left: 10px;
      }
    }
    .tc-choice{
      .list{
        margin-bottom:25px;
        span{
          flex:1;
        }
        .pri{
          vertical-align:middle;
          font-size: 12px;
        }
        .mar{
          margin-right: 16px;
        }
        &:last-of-type{
          margin-bottom: 14px;
        }
      }
      .tw{
        opacity: 0.5;
        font-size: 12px;
        color: #000000;
        margin-top: 11px;
      }
      background: #F9F9FA;
      padding:26px 22px;
      font-size: 14px;
      input{
        border: 1px solid #979797;
        border-radius: 2px;
        width:120px;
        height: 30px;
        text-align: center;
      }
      .detail{
        opacity: 0.5;
        font-size: 12px;
        color: #000000;
        margin-bottom: 2px;
        &:last-of-type{
          margin-top: 11px;
          margin-bottom: 10px;
          font-size: 18px;
          color: #4D4D4D;
          span{
            color:rgba(245,166,35,1);
          }
        }
      }

    }
  }
</style>



